// Pipeline Stages API v1
// GET /api/v1/pipelines/[pipelineId]/stages - List stages
// POST /api/v1/pipelines/[pipelineId]/stages - Create stage
// PUT /api/v1/pipelines/[pipelineId]/stages - Reorder stages

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'

// GET - List stages for a pipeline
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ pipelineId: string }> }
) {
  try {
    const { pipelineId } = await params
    
    const { data: stages, error } = await supabaseAdmin
      .from('PipelineStage')
      .select('*')
      .eq('pipelineId', pipelineId)
      .eq('isActive', true)
      .order('displayOrder', { ascending: true })
    
    if (error) throw error
    
    return NextResponse.json({
      success: true,
      data: stages || [],
    })
  } catch (error: any) {
    console.error('GET /api/v1/pipelines/[pipelineId]/stages error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to fetch stages' },
      { status: 500 }
    )
  }
}

// POST - Create stage
export async function POST(
  request: NextRequest,
  { params }: { params: Promise<{ pipelineId: string }> }
) {
  try {
    const { pipelineId } = await params
    const body = await request.json()
    const {
      name,
      description,
      color = '#6B7280',
      stageType = 'open',
      winProbability = 0,
      autoMoveAfterDays,
      notifyOnEntry = false,
    } = body
    
    if (!name) {
      return NextResponse.json(
        { success: false, error: 'Stage name is required' },
        { status: 400 }
      )
    }
    
    // Check if pipeline exists
    const { data: pipeline } = await supabaseAdmin
      .from('Pipeline')
      .select('id')
      .eq('id', pipelineId)
      .single()
    
    if (!pipeline) {
      return NextResponse.json(
        { success: false, error: 'Pipeline not found' },
        { status: 404 }
      )
    }
    
    // Check if stage name already exists
    const { data: existing } = await supabaseAdmin
      .from('PipelineStage')
      .select('id')
      .eq('pipelineId', pipelineId)
      .eq('name', name)
      .single()
    
    if (existing) {
      return NextResponse.json(
        { success: false, error: `Stage '${name}' already exists in this pipeline` },
        { status: 409 }
      )
    }
    
    // Get max display order
    const { data: maxOrderResult } = await supabaseAdmin
      .from('PipelineStage')
      .select('displayOrder')
      .eq('pipelineId', pipelineId)
      .order('displayOrder', { ascending: false })
      .limit(1)
      .single()
    
    const displayOrder = (maxOrderResult?.displayOrder || 0) + 1
    
    // Create stage
    const { data: stage, error } = await supabaseAdmin
      .from('PipelineStage')
      .insert({
        pipelineId,
        name,
        description,
        color,
        stageType,
        displayOrder,
        winProbability,
        autoMoveAfterDays,
        notifyOnEntry,
        isActive: true,
      })
      .select()
      .single()
    
    if (error) throw error
    
    return NextResponse.json({
      success: true,
      data: { stage },
    }, { status: 201 })
  } catch (error: any) {
    console.error('POST /api/v1/pipelines/[pipelineId]/stages error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to create stage' },
      { status: 500 }
    )
  }
}

// PUT - Reorder stages (bulk update)
export async function PUT(
  request: NextRequest,
  { params }: { params: Promise<{ pipelineId: string }> }
) {
  try {
    const { pipelineId } = await params
    const body = await request.json()
    const { stages } = body // Array of { id, displayOrder }
    
    if (!stages || !Array.isArray(stages)) {
      return NextResponse.json(
        { success: false, error: 'stages array is required' },
        { status: 400 }
      )
    }
    
    // Update each stage's display order
    for (const stage of stages) {
      if (stage.id && stage.displayOrder !== undefined) {
        await supabaseAdmin
          .from('PipelineStage')
          .update({ displayOrder: stage.displayOrder })
          .eq('id', stage.id)
          .eq('pipelineId', pipelineId)
      }
    }
    
    // Get updated stages
    const { data: updatedStages, error } = await supabaseAdmin
      .from('PipelineStage')
      .select('*')
      .eq('pipelineId', pipelineId)
      .eq('isActive', true)
      .order('displayOrder', { ascending: true })
    
    if (error) throw error
    
    return NextResponse.json({
      success: true,
      data: updatedStages,
    })
  } catch (error: any) {
    console.error('PUT /api/v1/pipelines/[pipelineId]/stages error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to reorder stages' },
      { status: 500 }
    )
  }
}
